//@flow
import React, { Component } from "react";
import styles from "./index.css";
import { View } from "react-web-dom";
import { Popover, Tag, Input, Modal } from 'antd';
import SelectGoods from "../../../../../public/selectGoods/index";
import SelectPage from "../../../../../public/selectPage/index";

const { TextArea } = Input;

export type LinkActionType = 'portal' | 'goods' | 'page' | 'url'
type Props = {
    getValues: Function,
    type: LinkActionType,
    value: any
}
type State = {
    urlValue: {
        url: string
    },
    selectGoodsVisible: boolean,
    selectPageVisible: boolean,
    inputUrlVisible: boolean,
}
export const linkInfo = {
    portal: {
        type: 'portal',
        name: '',
        alias: ''
    },
    goods: {
        type: 'goods',
        name: '',
        alias: ''
    },
    page: {
        type: 'page',
        name: '',
        alias: ''
    },
    url: {
        type: 'url',
        name: '',
        alias: ''
    }
}

export default class Index extends Component<Props, State> {
    state = {
        urlValue: {
            url: ''
        },
        selectGoodsVisible: false,
        selectPageVisible: false,
        inputUrlVisible: false
    }
    onClick = (linkAction: LinkActionType) => {
        const props = { ...this.props }
        switch (linkAction) {
            case 'portal':
                props.getValues({
                    ...props, ...{ type: linkAction }
                })
                break;
            case 'goods':
                this.setState({
                    selectGoodsVisible: true
                })
                break;
            case 'page':
                this.setState({
                    selectPageVisible: true
                })
                break;
            // TODO
            case 'url':
                this.setState({
                    inputUrlVisible: true
                })
                break;
        }
    }


    render() {
        const props = { ...this.props }
        const { type, getValues } = props
        const { selectGoodsVisible, selectPageVisible, inputUrlVisible, urlValue } = this.state
        return (
            <View>
                <View>
                    <Popover
                        placement="bottom"
                        content={
                            <View className={`${styles.actionLinkPopoverContent}`}>
                                <a onClick={() => {
                                    this.onClick('portal')
                                }}>{linkInfo.portal.name}</a>
                                <a onClick={() => {
                                    this.onClick('goods')
                                }}>{linkInfo.goods.name}</a>
                                <a onClick={() => {
                                    this.onClick('page')
                                }}>{linkInfo.page.name}</a>
                                <a onClick={() => {
                                    this.onClick('url')
                                }}>{linkInfo.url.name}</a>
                            </View>
                        }
                    >
                        <Tag color="#108ee9">{linkInfo[type].alias}</Tag>
                        <a type="primary"></a>
                    </Popover>
                </View>
                <SelectGoods
                    visible={selectGoodsVisible}
                    multiSelect={false}
                    close={() => {
                        this.setState({
                            selectGoodsVisible: false
                        })
                    }}
                    onOk={(state) => {
                        this.setState({
                            selectGoodsVisible: false
                        }, () => {
                            getValues({
                                ...props, ...{
                                    type: 'goods',
                                    value: {
                                        id: state.checkedData[0].id
                                    }
                                }
                            })
                        })
                    }}
                />
                <SelectPage
                    visible={selectPageVisible}
                    close={() => {
                        this.setState({
                            selectPageVisible: false
                        })
                    }}
                    getState={(state) => {
                        this.setState({
                            selectPageVisible: false
                        }, () => {
                            getValues({
                                ...props, ...{
                                    type: 'page',
                                    value: {
                                        id: state.value.id
                                    },
                                }
                            })
                        })
                    }}
                />
                <Modal
                    title=""
                    visible={inputUrlVisible}
                    onCancel={() => {
                        this.setState({
                            inputUrlVisible: false
                        })
                    }}
                    onOk={(e) => {
                        this.setState({
                            inputUrlVisible: false
                        }, () => {
                            getValues({
                                ...props, ...{
                                    type: 'url',
                                    value: urlValue
                                }
                            })
                        })
                    }}
                    style={{ top: 20 }}
                    width={756}
                >
                    <TextArea
                        placeholder=""
                        autosize={{ minRows: 2, maxRows: 6 }}
                        value={urlValue && typeof(urlValue['url']) !== "undefined" ? urlValue.url : ''}
                        onChange={(e) => {
                            this.setState({
                                urlValue: {
                                    url: e.target.value
                                }
                            })
                        }} />
                </Modal>
            </View>
        )
    }
}
