//@flow
import React, { Component } from "react";
import { Icon, Input, Row, Col, Radio, Modal, message } from "antd";
import styles from "./index.css";
import { View } from "react-web-dom";
import UploadImage from "../../../../uploadImage/index";
import ActionLink, { linkInfo } from "../common/actionLink"

const RadioGroup = Radio.Group;
type LinkActionType = 'portal' | 'goods' | 'page' | 'url'

type Props = {
    componentName?: string,
    options: {
        layout_style: number
    },
    data: Array<{
        img: {
            url: string
        },
        title: string,
        link: {
            action: LinkActionType,
            param: {}
        }
    }>,
    getValues: Function
}
type State = {}

export default class Index extends Component<Props, State> {
    static defalutProps = {
        componentName: 'imageAds'
    }

    render() {
        let { options, data, getValues } = this.props
        const { layout_style } = options
        return (
            <View className={`${styles.imageAdsCtrlWarp} imageAdsCtrlWarp`}>
                <Row>
                    <Col span={5}></Col>
                    <Col span={19}>
                        <RadioGroup
                            value={layout_style}
                            onChange={(e) => {
                                getValues({
                                    options: { ...options, ...{ layout_style: e.target.value } },
                                    data
                                })
                            }}
                        >
                            <Radio value={1}></Radio>
                            <Radio value={2}></Radio>
                        </RadioGroup>
                    </Col>
                </Row>
                <Row>
                    <Col span={5}></Col>
                    <Col span={19}>
                        <p>640px</p>
                    </Col>
                </Row>
                {
                    Array.isArray(data) && data.length > 0 ? data.map((listItem, index) => (
                        <View
                            className={styles.imageAdsCtrlItem}
                            key={index}
                        >
                            <p className={styles.imageAdsCtrlItemTop}>
                                {
                                    index > 0 ?
                                        <a
                                            onClick={() => {
                                                let _data = data
                                                let add = [_data[index], _data[index - 1]]
                                                _data.splice(index - 1, 2, ...add)
                                                getValues({
                                                    options,
                                                    data: _data
                                                })
                                            }}
                                        >
                                            
                                        </a> : null
                                }
                                {
                                    index < data.length - 1 ?
                                        <a
                                            onClick={() => {
                                                let _data = data
                                                let add = [_data[index + 1], _data[index]]
                                                _data.splice(index, 2, ...add)
                                                _data.splice(index - 1, 2, ...add)
                                                getValues({
                                                    options,
                                                    data: _data
                                                })
                                            }}
                                        >
                                            
                                        </a> : null
                                }
                                <a
                                    onClick={() => {
                                        Modal.confirm({
                                            title: '',
                                            content: (
                                                <View>
                                                    <p>
                                                        
                                                        {
                                                            listItem.img.url.length ?
                                                                <img
                                                                    alt=''
                                                                    src={listItem.img.url}
                                                                    style={{ width: 80 }}
                                                                /> : ''
                                                        }
                                                    </p>
                                                    <p>
                                                        {linkInfo[listItem.link.action].alias}
                                                    </p>
                                                </View>
                                            ),
                                            okText: '',
                                            okType: 'danger',
                                            cancelText: '',
                                            onOk() {
                                                let _data = data
                                                _data.splice(index, 1)
                                                getValues({
                                                    options,
                                                    data: _data
                                                })
                                                message.success('', 1)
                                            },
                                            onCancel() {
                                            },
                                        })
                                    }}
                                >
                                    
                                </a>
                            </p>
                            <View className={styles.imageAdsCtrlItemBot}>
                                <View className={styles.imageAdsCtrlItemLeft}>
                                    <UploadImage
                                        onChange={(e) => {
                                            let _data = data
                                            _data[index].img = { url: e }
                                            getValues({
                                                options,
                                                data: _data
                                            })
                                        }}
                                        is_save={1}
                                    >
                                        {
                                            typeof listItem.img.url === 'string' && listItem.img.url.indexOf('http') > -1 ?
                                                <img
                                                    src={listItem.img.url}
                                                    alt=''
                                                    style={{ width: '80px' }}
                                                /> :
                                                <View className={styles.uploadBtn}>
                                                    <Icon type='plus' />
                                                    <p></p>
                                                </View>
                                        }
                                    </UploadImage>
                                </View>
                                <View className={styles.imageAdsCtrlItemRight}>
                                    <p>
                                        <span></span>
                                        <Input
                                            style={{ width: 240 }}
                                            placeholder=''
                                            value={listItem.title}
                                            onChange={(e) => {
                                                let _data = data
                                                _data[index].title = e.target.value
                                                getValues({
                                                    options,
                                                    data: _data
                                                })
                                            }}
                                        />
                                    </p>
                                    <div className={styles.imageAdsCtrlItemRightRow}>
                                        <span></span>
                                        <ActionLink
                                            type={listItem.link.action}
                                            selectGoodsVisible={false}
                                            selectPageVisible={false}
                                            inputUrlVisible={false}
                                            getValues={(state) => {
                                                let _data = data
                                                _data[index].link.action = state.type
                                                _data[index].link.param = state.value
                                                getValues({
                                                    options,
                                                    data: _data
                                                })
                                            }}
                                            value={() => {
                                                switch (listItem.link.action) {
                                                    case 'portal':
                                                        return
                                                    case 'goods':
                                                        return listItem.link.param
                                                    case 'page':
                                                        return listItem.link.param
                                                    case 'url':
                                                        return listItem.link.param
                                                }
                                            }}
                                        />
                                    </div>
                                </View>
                            </View>
                        </View>
                    )) : null
                }
                <View
                    className={styles.imageAdsCtrlItemAdd}
                    onClick={() => {
                        let _data = [...data, {
                            img: {
                                url: require('../../../../../images/page/view/image-ads-default.png')
                            },
                            title: '',
                            link: {
                                action: 'portal',
                                param: {}
                            }
                        }]
                        getValues({
                            options,
                            data: _data
                        })
                    }}
                >
                    <Icon type='plus' /> 
                </View>
            </View>
        )
    }
}
