//@flow
import React, { Component } from "react";
import { Modal, message } from "antd";
import { View } from "react-web-dom";
import { SortableContainer, SortableElement, arrayMove } from 'react-sortable-hoc';
import Goods from "./goods";
import GoodsList from "./goodsList";
import GoodsSearch from "./goodsSearch";
import Separator from "./separator";
import ImageAds from "./imageAds";
import ImageNav from "./imageNav";
import ShopWindow from "./shopWindow";
import Video from "./video";
import TopMenu from "./topMenu";
import Title from "./title";
import TextNav from "./textNav";
import AuxiliaryBlank from "./auxiliaryBlank";
import type { optionsType, PageBodyType } from '../../../../interfaces/page'
import styles from "./index.css";

const SortableItem = SortableElement(({ value }) =>
    <li className={styles.sortableListLi}>{value}</li>
);
const SortableList = SortableContainer(({ items, pressDelay }) => {
    return (
        <ul className={styles.sortableList}>
            {items.length > 0 ? items.map((value, index) => (
                <SortableItem key={`item-${index}`} index={index} value={value} />
            )) : null}
        </ul>
    );
});
type Props = {
    options: optionsType,
    body: PageBodyType,
    backgroundColor: string,
    onViewItemClick: Function,
    onHeaderClick: Function,
    setPage: Function,
}
type State = {
    hoverShow: number,
    items: any,
    actioning: boolean
}
type SortEndType = { oldIndex: number, newIndex: number }
type SortStartType = { node: any, index: number, collection: any }
type EventType = {
    target: {
        innerHTML: string,
        dataset: {
            index: number
        }
    }
}
export default class PageView extends Component<Props, State> {
    state = {
        hoverShow: -2,
        items: [],
        actioning: false
    }
    onSortEnd = (param: SortEndType, event: EventType) => {
        // 
        if (this.state.actioning === false) {
            const { body, setPage } = this.props
            let _body = [...body]
            _body = arrayMove(_body, param.oldIndex, param.newIndex)
            let type = body[param.oldIndex].type
            setPage({
                options: {
                    type: type,
                    index: param.newIndex
                },
                body: _body,
            })
        }
    }
    onSortStart = (param: SortStartType, event: EventType) => {
        this.props.onViewItemClick()
        const text = event.target.innerHTML
        if (text === '' || text === '' || text === '') {
            this.setState({
                actioning: true
            }, () => {
                switch (text) {
                    case '':
                        this.delete(event)
                        break
                    case '':
                        this.up(event)
                        break
                    case '':
                        this.down(event)
                        break
                    default:
                        break
                }
            })
        } else {
            this.setState({
                actioning: false
            })
        }
    }
    delete = (event: EventType) => {
        const { body, setPage } = this.props
        const index = parseInt(event.target.dataset.index)
        const type = body[index].type
        let title = ''
        switch(type){
           case 'goods':
                title = ''
                break
            case 'goods_list':
                title = ''
                break
            case 'goods_search':
                title = ''
                break
            case 'separator':
                title = ''
                break
            case 'auxiliary_blank':
                title = ''
                break
            case 'image_ads':
                title = ''
                break
            case 'image_nav':
                title = ''
                break
            case 'shop_window':
                title = ''
                break
            case 'video':
                title = ''
                break
            case 'top_menu':
                title = ''
                break
            case 'title':
                title = ''
                break
            case 'text_nav':
                title = ''
                break
        }
        Modal.confirm({
            title: '',
            content: `${title}`,
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk() {
                let _body = body.concat()
                _body.splice(index, 1)
                if (Array.isArray(_body) && _body.length > 0) {
                    setPage({
                        options: {
                            type: _body[0]['type'] ? _body[0]['type'] : null,
                            index: 0,
                        },
                        body: _body,
                    })
                }else{
                    setPage({
                        options: {
                            type: null,
                            index: 0,
                        },
                        body: [],
                    })
                }
                message.success('', 1)
            }
        })
    }
    up = (event: EventType) => {
        const { body, setPage } = this.props
        const index = parseInt(event.target.dataset.index)
        const item = body[index]
        let _body = arrayMove(body, index, index - 1)
        setPage({
            options: {
                type: item.type,
                index: index - 1,
            },
            body: _body,
        })
    }
    down = (event: EventType) => {
        const { body, setPage } = this.props
        const index = parseInt(event.target.dataset.index)
        const item = body[index]
        let _body = arrayMove(body, index, index + 1)
        setPage({
            options: {
                type: item.type,
                index: index + 1,
            },
            body: _body,
        })
    }

    render() {
        const { hoverShow } = this.state
        const { body, options, onHeaderClick, backgroundColor } = this.props

        const items = body.length > 0 ? body.map((item, index) => (
            <View
                key={index}
                className={styles.phoneItem}
                style={
                    options.index === index ?
                        {
                            boxShadow: 'rgb(181, 181, 181) 0px 0px 20px 0px',
                        } : {}
                }
                onMouseEnter={() => {
                    if (index !== options.index) {
                        this.setState({ hoverShow: index })
                    }
                }}
                onMouseLeave={() => {
                    this.setState({ hoverShow: -2 })
                }}
            >
                {
                    this.returnContain({ type: item.type, title: item.title }, index)
                }
                {
                    (options.index === index) || (hoverShow === index && hoverShow !== options.index) ?
                        <View
                            className={styles.operation}
                            onClick={(e) => {
                                e.stopPropagation()
                            }}
                        >
                            {
                                index > 0 ? <a data-index={index}></a> : null
                            }
                            {
                                index < body.length - 1 ? <a data-index={index}></a> : null
                            }
                            <a data-index={index}></a>
                            <a data-index={index}></a>
                        </View>
                        : null
                }
            </View>
        )) : []
        return (
            <View className={styles.dragPhoneWarp}>
                <View
                    className={styles.dragPhoneHeader}
                    onClick={onHeaderClick}
                >
                    <span></span>
                    <img
                        src={require('../../../../images/shop/page-setting.png')}
                        alt='diyPhone'
                    />
                </View>
                <View
                    className={styles.dragPhoneContain}
                    style={backgroundColor ? { backgroundColor } : { backgroundColor: '#FFFFFF' }}
                >
                    <SortableList items={items} onSortEnd={this.onSortEnd} onSortStart={this.onSortStart} />
                </View>
            </View>
        )
    }

    returnContain(item: { type: string, title: string }, index: number) {
        const { body } = this.props
        const { options, data } = body[index]
        switch (item.type) {
            case 'goods':
                return <Goods options={options} data={data} />
            case 'goods_list':
                return <GoodsList options={options} data={data} />
            case 'goods_search':
                return <GoodsSearch options={options} data={data} />
            case 'separator':
                return <Separator options={options} data={data} />
            case 'auxiliary_blank':
                return <AuxiliaryBlank options={options} data={data} />
            case 'image_ads':
                return <ImageAds options={options} data={data} />
            case 'image_nav':
                return <ImageNav options={options} data={data} />
            case 'shop_window':
                return <ShopWindow options={options} data={data} />
            case 'video':
                return <Video options={options} data={data} />
            case 'top_menu':
                return <TopMenu options={options} data={data} />
            case 'title':
                return <Title options={options} data={data} />
            case 'text_nav':
                return <TextNav options={options} data={data} />
            default:

        }
    }
}
